.dialog {
   width: 100%;
   border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
.dialog__name {
   width: 100%;
   height: 40px;
   display: flex;
   align-items: center;
   font-size: 16px;
   background-color: #fff;
   padding: 0 0 0 20px;
}
.dialog__name:hover {
   background-color: #efefef;
}
